@import "./../../../styles/commonStyle/commonStyle.scss";

.search-filter {
  background: $color-white;
  padding: 30px 0;
  margin-bottom: 30px;
  .page-heading {
    letter-spacing: 1.57px;
    color: #0289ba;
    font-size: $font48;
    font-weight: bold;
    text-transform: uppercase;
  }
  .filter-dropdown {
    font-size: $font18;
    &:hover {
      background: none;
    }
  }
  .selected-filters {
    padding-top: 10px;
    button {
      display: inline-block;
      padding: 5px 28px 5px 8px;
      font-size: $font12;
      background: #fecc04;
      color: $color-black-variant;
      text-align: left;
      margin: 5px;
      position: relative;
      //box-shadow: 3px 3px 5px $color-box-shadow;
      border-radius: 5px;
      svg {
        position: absolute;
        top: 7px;
        right: 5px;
        vertical-align: top;
        cursor: pointer;
        background-color: $color-white;
        border-radius: 10px;
        margin-left: 5px;
      }
      &.filter-clear-all {
        border: 1px solid #979797;
        background: transparent;
        padding: 6px 8px;
      }
    }
  }
  .filters {
    padding: 10px;
    min-width: 140px;
    min-height: 300px;
    &.brands {
      flex: 1;
      .filter-list {
        width: 145px;
      }
    }
    .filter-heading {
      font-size: $font14;
      border-bottom: 2px solid #5f4f3d7f;
      padding: 5px 0;
      font-weight: 600;
    }
    .filter-list {
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      max-height: 290px;
      width: 100%;
      li {
        list-style: none;
        margin: 2px 0;
        width: 100%;
        button {
          cursor: pointer;
          padding: 5px 8px;
          font-size: $font12;
          display: inline-block;
          text-align: left;
          &:hover {
            background-color: transparent;
          }
        }
        &.selected {
          button {
            background: $color-yellow;
            color: $color-black-variant;
            //box-shadow: 3px 3px 5px $color-box-shadow;
            border-radius: 5px;
          }
        }
      }
    }
  }
}
