@mixin colorBox($c1, $c2, $c3, $c4) {
  color: $c1;
  border: 2px solid $c1;

  &.selected,
  &.sub-heading {
    background: linear-gradient($c2, $c3);
    color: $c4;
  }
}
@mixin colorBoxPallete {
  &.occasion {
    @include colorBox(#9b1ab0, #b3209a, #711cd3, #ffffff);
  }
  &.flavor,
  &.flavour {
    @include colorBox(#ff5b06, #f7aa16, #f74905, #ffffff);
  }
  &.snack-type {
    @include colorBox(#ba1118, #f80a27, #bd0218, #ffffff);
  }
  &.brand {
    @include colorBox(#0a66cd, #06abba, #0c4cb4, #ffffff);
  }
}
.shop-by-category {
  &-heading {
    @media (max-width: 767px) {
      display: block;
    }
  }
  &-container {
    display: block;
    width: 100%;
    &-heading {
      display: flex;
      width: 100%;
      align-content: space-around;
      @media (max-width: 767px) {
        display: block;
      }
    }
    &-inner {
      max-width: 1160px;
      flex-wrap: wrap;
      margin: 0 auto;
      display: flex;
    }
  }
  &-next {
    margin-top: 20px;
    margin-bottom: 20px;
    button {
      padding: 6px 20px !important;
      min-width: 128px !important;
      font-size: 1em;
      text-align: center;
      line-height: normal;
      letter-spacing: normal;
      color: #231f20;
      background-color: #ffcc00;
      border-color: #ffcc00;
      &:hover {
        background-color: #e2b502;
        border-color: #e2b502;
      }
    }
    span {
      font-size: 0.875em;
      display: block;
    }
  }
  &-category {
    display: block;
    width: 100%;
    &-item {
      position: relative;
      cursor: pointer;
      @include colorBoxPallete;
    }
    img {
      max-height: 258px;
      width: 100%;
      margin: 0 auto;
    }
    .category-heading {
      color: inherit;
      text-align: center;
    }
    .sub-heading {
      padding: 6px 16px;
      @include colorBoxPallete;
    }

    .sub-category {
      .heading {
        margin: 40px auto;
      }
    }
  }
}
