@import './../../styles/commonStyle/commonStyle.scss';
.is-home{
  .main-contain-inner{
    background-image: none;
  }
  .container-root{
    max-width: 1190px !important;
  }
}
.home{
  &-wrapper{
    background: url(/images/home-bg.png);
    background-position-y: 550px;
    background-size: 100%;
    background-repeat: no-repeat;
  }
  &-row{
    padding-top: 190px;
    &:first-child{
      &>div{
        &:first-child{
          flex: 1;
        }
        &:last-child{
          max-width: 620px;
          flex-basis: 620px;
        }
      }
      .home-text{
        padding-left: 34px;
        &:after{
          left: 0;
        }
      }
    }
    &:last-child{
      flex-flow: row-reverse;
      padding-top: 350px;
      &>div{
        &:first-child{
          flex: 1;
        }
        &:last-child{
          max-width: 570px;
          flex-basis: 570px;
        }
      }
      .home-text{
        text-align: right;
        padding-right: 34px;
        &:after{
          right: 0;
        }
      }
    }
    img{
      max-width: 100%;
    }
    &-container{
      margin-top: 20px;
    }
  }
  &-text{
    position: relative;
    &:after{
      content: '';
      height: 100%;
      width: 14px;
      background-color: $yellow;
      position: absolute;
      top: 0;
    }
  }
  &-title{
    @include homeHeading($white);
    margin-bottom: 15px;
  }
  &-content{
    @include pageContent($white);
    margin-bottom: 25px;
    padding-left: 5px;
  }
  &-link{
    @include standardLink($yellow, $yellow-dark);
    padding-left: 5px;
  }
}
.hero {
  margin-bottom: 40px;
  &-text {
    width: 90%;
    margin: 0 auto;
    .hero-header{
      text-align: center;
      @include homeHeading($blue-light);
    }
    .hero-content{
      text-align: center;
      @include pageContent($black-light);
    }
  }
  img {
    &.hero-background {
      width: 100%;
    }
  }
  .scroll-to {
    position: absolute;
    left: 50%;
    bottom: 145px;
    transform: translateX(-50%);
    border: 3px solid $white;
    border-radius: 50%;
    height: 68px;
    width: 68px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover{
      
    }
    svg{
      font-size: 50px;
    }
  }
}

.brought-to-you{
  display: inline-flex;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 50px;
  img{
    max-height: 50px;
    max-width: 70px;
  }
  .text{
    padding: 0 15px;
    @include pageContent($blue-light);
    font-weight: 600;
  }
}