@import "./../../../styles/commonStyle/commonStyle.scss";
.header{
  box-shadow: none !important;
  background-image: linear-gradient(to right, #cc2500 , #f2a326);
  border-bottom: 10px solid $blue;
  height: 100px;
  padding: 15px 0 15px 0;
  z-index: 1301 !important;
  @media (max-width: $mobile-max-width){
      height: 90px;
      padding: 10px 5px 18px 0;
  }
  @media (max-width: 600px){
    padding: 18px 5px 18px 0;
    border-bottom: 5px solid $blue;
  }
  @media (max-width: 420px){
      height: 65px;
  }
  .user{
      &-list{
          padding-right: 0 !important;
          padding-left: 5px !important;
          position: relative;
          @media (max-width: 767px){
              display: none;
          }
          &-wrapper{
              @media (min-width: 768px) and (max-width: 1200px){
                  min-width: 140px;
              }
              ul{
                  position: absolute;
                  top: 40px;
                  width: 100%;
                  min-width: 140px;
                  padding-top: 0 !important;
                  li{
                      font-weight: 300;
                  }
                  i{
                      margin-right: 12px;
                      min-width: 12px;
                      margin-left: 3px;
                  }
              }
              &.open{
                  position: relative;
                  &:before{
                      content: '';
                      border: 1px solid $blue;
                      box-shadow: 4px 4px 6px $color-box-shadow;
                      border-radius: 17px 6px 6px 6px;
                      position: absolute;
                      top: 0;
                      left: 0;
                      width: 100%;
                      height: 120px;
                      min-width: 140px;
                      background-color: $color-white;
                  }
                  .user{
                      &-name{
                          color: $blue;
                      }
                      &-icon{
                        border-color: $blue;
                        svg{
                            color: $blue;
                        }
                      }
                  }
              }
          }
      }
      &-name{
          margin-left: 5px;
          color: $white;
          font-size: 18px;
          white-space: nowrap;
          max-width: 180px;
          text-overflow: ellipsis;
          overflow: hidden;
          text-align: left;
          min-height: 33px;
          @media (max-width: 1023px){
              min-height: auto;
          }
          @media (max-width: 959px){
              font-size: 16px;
          }
          @media (max-width: 480px){
              max-width: 120px;
          }
          @media (max-width: 420px){
              max-width: 100px;
              font-size: 14px;
          }
          @media (max-width: 380px){
              max-width: 70px;
          }
          @media (max-width: 359px){
              max-width: 50px;
          }
      }
      &-button{
          color: #4B4B4B;
          text-transform: capitalize;
          min-width: auto;
          @media (max-width: 420px){
              padding-left: 0;
          }
      }
      &-icon{
          display: flex;
          border: 1px solid $white;
          padding: 1px;
          border-radius: 50%;
          svg{
              color: $white;
          }
      }
  }
  &>div{
      padding: 0px;
      @media (max-width: 420px){
          min-height: auto;
          height: 100%;
      }
  }
  .logo{
      max-height: 75px;
      @media (max-width: $mobile-max-width){
          max-width: 90px;
      }
      @media (max-width: 959px){
          max-width: 70px;
          max-height: 35px
      }
      &-wrapper{
          a{
            position: absolute;
            bottom: -12px;
            left: 50%;
            transform: translate(-50%, 50%);
          }
          @media (max-width: 959px) {
              display: flex;
              align-items: center;
          }
      }
  }
  .container-root{
      margin-bottom: 0 !important;
      max-width: 1300px !important;
      padding-left: 0 !important;
      padding-right: 0 !important;
  }
}

// menu links for header
.menu{
  &-list{
      padding: 0 !important;
      margin-left: -8px;
      margin-right: -8px;
      justify-content: flex-end;
      display: flex;
      align-items: center;
      &-single{
          width: auto !important;
          padding-left: 20px !important;
          padding-right: 20px !important;
          @media (max-width: 959px){
              padding-left: 0 !important;
              padding-right: 0 !important;
          }
      }
      .shipto{
          &-btn{
              padding: 1px 16px;
              font-size: $font14;
              text-transform: uppercase;
              min-width: 150px !important;
              border-radius: 4px;
              border: 2px solid $color-white;
              background-color: transparent;
              color: $color-white;
              @media (max-width: 959px) and (min-width: 767px){
                  margin-right: 10px;
              }   
              @media (max-width: 767px){
                  min-width: auto !important;
                  border: none;
                  padding: 5px;
                  margin-right: 0;
                  color: $color-white;
                  margin-right: 5px;
              }
              &.open{
                  border-radius: 4px 4px 0 0;
              }
              &:hover, &:focus{
                background-color: transparent;
                @media (min-width: 767px){
                    border: 2px solid $color-white;
                }
              }
              &.Mui-disabled{
                color: $color-white;
              }
              .shipto-text{
                  font-weight: 600;
                  margin-right: 5px;
              }
              .shipto-zip{
                  font-weight: 600;
              }
          }
      }
      .cart{
          &-list{
              @media (min-width: 959px){
                  padding-left: 0 !important;
                  padding-right: 0 !important;
              }
              button{
                  color: $color-white;
              }
              svg{
                  width: 22px;
              }
          }
          &-count{
              top: 4px;
              color: #fff;
              left: 29px;
              height: 18px;
              min-width: 18px;
              line-height: 16px;
              padding: 1px;
              position: absolute;
              font-size: 12px;
              background: $color-red;
              font-weight: 600;
              border-radius: 50%;
              @media (max-width: $mobile-max-width){
                  left: 24px;
                  top: 3px;
              }
          }
      }
  }
  &-link{
      color: $color-white;
      font-size: $font14;
      font-weight: 600;
      text-transform: uppercase;
      text-decoration: none;
      position: relative;
      @media (max-width: 767px){
          padding-left: 0;
          padding-right: 0;
      }
      &.product-link{
          border: none;
          color: #4B4B4B;
          text-decoration: none;
          font-size: 1em;
          font-weight: 600;
          letter-spacing: normal;
          line-height: normal;
          &:hover{
              text-decoration: none;
              color: #666;
          }
          span:last-child{
              display: none;
          }
      }
      &.menu-icon{
          color: $color-white;
          display: none;
          @media (max-width: 959px){
              display: block;
          }
          svg{
              width: 30px;
              height: 30px;   
          }
          &-close{
              position: absolute;
              right: 0;
              top: 11px;
              color: $color-white;
          }
      }
      &.active, &:hover{
          color: $color-yellow;
      }
      &.no-active, &.no-active:hover{
        color: $color-white;
      }
      &-web{
          ul{
              @media (max-width: 959px){
                  justify-content: flex-end;
              }
          }
      }
      &.btn-blue{
          padding: 6px 20px !important;
          min-width: 128px !important;
          font-size: 1em;
          text-align: center;
          line-height: normal;
          letter-spacing: normal;
          color: #231f20;
          &:after{
              opacity: 0;
          }
          &:hover, &:focus{
              background-color: #e2b502;
              border-color: #e2b502;
          }
          @media (max-width: 959px){
              min-width: 80px !important;
              margin-left: 5px;
          }
          @media (max-width: 767px){
              padding-left: 0 !important;
              padding-right: 0 !important;
          }
      }
      &-wrapper {
          display: flex;
          padding-left: 30px !important;
          justify-content: flex-end;
          &.loggedin{
              justify-content: space-between;
          }
          @media (max-width: $mobile-max-width) {
              margin-top: 0 !important;
              padding-left: 0 !important;
              flex-flow: column;
          }
      }
  }
}

// drawer css
.drawer{
  &-wrapper{
      @media (max-width: 959px){
          z-index: 1303 !important;
      }
  }
  &-mobile{
      background-image: linear-gradient(to bottom, red , orange);
      @media (max-width: 959px){
          min-height: 100vh;
          width: calc(100vw - 50px);
          padding-top: 0;
          box-shadow: none !important;
      }
      .menu-link-wrapper {
          @media (max-width: $mobile-max-width) {
              padding: 0 !important;
          }
      }
      .menu-list-single{
          @media (max-width: $mobile-max-width) {
              padding: 0 !important;
              border-bottom: 1px solid rgba(255,255,255,0.4);
              width: 100% !important;
              a{
                  padding: 10px 24px;
                  display: block;
                  width: 100%;
              }
          }
      }
      .login-list{
          padding: 15px 40px 15px 0px !important;
          min-height: 70px;
      }
      .user-icon{
          display: flex;
          margin-right: 5px;
      }
      .user-button{
          text-transform: capitalize;
          justify-content: flex-start;
          padding-left: 20px;
          text-align: left;
      }
  }
}
.reg-options{
  position: relative;
  button{
      min-width: auto;
      color: $color-white;
  }
  ul{
      position: absolute;
      top: 40px;
      left: 50%;
      transform: translateX(-50%);
      background-color: #fff;
      border: 1px solid $blue;
      box-shadow: 4px 4px 6px rgba(0,0,0,.160784);
      border-radius: 6px;
      padding: 2px;
      @media (max-width: 1024px){
          transform: none;
          left: auto;
          right: -30px;
      }
      &:before{
          content: '';
          position: absolute;
          width: 10px;
          height: 10px;
          border-top: 1px solid $blue;
          border-left: 1px solid $blue;
          transform: translateX(-50%) rotate(45deg);
          background: #fff;
          top: -6px;
          left: 50%;
          @media (max-width: 1024px){
              transform: translateX(0) rotate(45deg);
              left: auto;
              right: 35px;
          }
      }
      li{
          font-size: 15px;
          padding-left: 10px;
          padding-right: 10px;
          @media (max-width: 599px){
              padding-left: 5px;
              padding-right: 5px;
              font-size: 13px;
              min-height: auto;
          }
          span{
              display: flex;
              margin-right: 2px;
              svg{
                  @media (max-width: 599px){
                      font-size: 1.3rem;
                  }
              }
          }
      }
  }
}

.popover{
  &-zip{
      z-index: 1302 !important;
      &-paper{
          border-top: none;
          border-radius: 0 !important;
          width: 150px !important;
          top: 60px !important;
          padding: 8px;
          @media (max-width: $mobile-max-width){
              top: 80px !important;
          }
          @media (max-width: 959px){
              top: 62px !important;
          }
          @media (max-width: 767px){
              border: 1px solid #c4c4c4;
              border-radius: 10px !important;
              top: 80px !important;
          }
          @media (max-width: 420px){
              top: 60px !important;
          }
          div{
              width: 100%;
          }
          .button-group{
              margin-top: 10px;
              display: flex;
              justify-content: space-between;
              .btn-mui{
                  font-size: $font12;
                  min-width: auto;
                  padding: 2px 5px;
                  width: calc(50% - 5px);
                  margin: 0;
                  &:not(:last-child){
                    margin-right: 5px;
                    @media (max-width: $mobile-max-width){
                        margin-bottom: 0;
                    }
                  }
              }
              &+span{
                  display: none;
              }
          }
          input{
              border: 1px solid #ccc;
              padding: 3px 8px;
              font-size: $font14;
              border-radius: 4px;
              width: 100%;
          }
      }
  }
}