@import "./commonStyle/commonStyle.scss";

* {
    box-sizing: border-box;
}

*:not(.material-icons):not(.fa) {
  font-family: "Poppins", sans-serif !important;
}
.Mui-focusVisible{
    outline: -webkit-focus-ring-color auto 2px !important;
    outline-color: $color-black !important;
    border-radius: 2px;
    span[class*='MuiTouchRipple']{
        display: none;
    }
}
a{
    &:focus{
        outline-color: $color-black;   
    }
}

/* normalize css */
html, body {
    border: 0;
    height: 100%;
    margin: 0;
    padding: 0;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Poppins", sans-serif !important;
  font-size: 1em !important;
  background-color: rgba(0,0,0,0) !important;
  @media (max-width: $mobile-max-width){
    font-size: 0.875em !important;
  }
  &.has-toast{
      .header{
        top: 48px;
        @media (max-width: 767px){
            top: 40px;
        }
        @media (max-width: 420px){
            height: 110px;
        }
      }
      .main-contain{
          &-inner{
            margin-top: 135px;
            @media (max-width: 420px){
                margin-top: 110px;
            }
          } 
      }
      .popover-zip-paper{
        top: 108px !important;
        @media (max-width: $mobile-max-width){
            top: 127px !important;
        }
        @media (max-width: 959px){
            top: 109px !important;
        }
        @media (max-width: 767px){
            top: 115px !important;
        }
        @media (max-width: 420px){
            top: 100px !important;
        }
      }
      .homepage-authenticated-user{
        padding-top: 34px;
        @media (max-width: $mobile-max-width){
            padding-top: 20px;
        }
        .home-offer{
            top: 135px !important;
            @media (max-width: 420px){
                top: 110px !important;
            }
        }
      }
  }
}

// button css
button, a{
    &.btn-mui {
        padding-top: 4px;
        padding-bottom: 4px;
        border: 1px solid;
        min-width: 140px;
        border-radius: 20px;
        box-shadow: none;
        color: #231f20;
        transition: all .5s ease;
        text-transform: none;
        font-size: 1.125rem;
        font-weight: 500;
        &:hover{
            border-width: 1px;
            box-shadow: none;
        }
        &.btn-sm {
            padding-top: 4px;
            padding-bottom: 4px;
            min-width: 100px;
        }
      }
    &.btn-blue {
        background-color: $color-yellow;
        border-color: $color-yellow;
        &:hover{
            background-color: $color-yellow-hover;
            border-color: $color-yellow-hover;
        }
        &[disabled] {
            border-color: $color-disable;
            background-color: $color-disable; 
            color:rgba(75, 75, 75, 0.5);
        }
    }
    &.btn-yellow {
        background-color: $color-yellow;
        border-color: $color-yellow;
        &:hover{
            background-color: $color-yellow-hover;
            border-color: $color-yellow-hover;
        }
        &[disabled] {
            border-color: $color-disable;
            background-color: $color-disable; 
            color:rgba(75, 75, 75, 0.5);
        }
    }
    &.btn-outlined{
        border: 1px solid $color-grey;
        background-color: #fff;
        font-size: 1.125rem;
        font-weight: 500;
        &:hover{
            background-color: $color-yellow-hover;
            border-color: $color-yellow-hover;
        }
    }
    &.btn-link{
        padding: 0;
        color: $color-primary;
        text-decoration: underline;
        min-width: auto;
        text-transform: capitalize;
        background-color: transparent;
        box-shadow: none;
        &:hover{
            background-color: transparent;
            text-decoration: underline;
        }
        &.Mui-disabled{
            background-color: transparent;
            color: rgba(0, 133, 202, 0.8);
        }
    }
    &.btn-red {
        background: $color-white;
        border: 2px solid $color-red;
        color: $color-red !important;
        &:hover{
            border-width: 2px;
            cursor: pointer;
            background-color: $color-red;	
            color: $color-white !important;
        }
    }
}
a{
    &.btn-red {
        padding: 6px 16px;
    }
}

// button group
.button{
    &-group{
        margin-top: 30px;
        @media (max-width: $mobile-max-width){
            width: 100%;
            padding: 0px;
        }
        .btn-mui{
            &:not(:last-child){
                margin-right: 20px;
                @media (max-width: $mobile-max-width){
                    margin-right: 0;
                    margin-bottom: 10px;
                }
            }
        }
    }
}

// filter button and modal
button{
    &.filter-btn{
        display: none;
        padding: 0px;
        @media (max-width: 767px){
            display: block;
        }
        &-close{
            display: none;
            float: right;
            padding: 5px 10px;
            color: #231f20;
            @media (max-width: 767px){
                display: block;
            }
        }
        label {
            font-size: 0.688em;
        }
        img {
            width: 24px;
        }
    }
}

// close icon
.icn-close{
    padding: 0 !important;
    min-width: auto !important;
    box-shadow: 0px 2px 4px #00000080;
    border-radius: 50% !important;
    position: absolute !important;
    top: -15px;
    right: 27px;
    background: #fff !important;
    i{
        color: #262626;
        width: 30px;
        height: 30px;
        position: relative;
        border-radius: 50%;
        font-size: 18px;
        &:before{
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            -webkit-text-stroke: 2px #fff;
            padding-left: 1px;
        }
    }
}

// position css
.pos{
    &-relative{
        position: relative;
    }
}

// font css
.font{
    // font weight
    &-400 {
        font-weight: 400 !important;
    }
    &-500 {
        font-weight: 500 !important;
    }
    &-600{
        font-weight: 600 !important;
    }
    &-700{
        font-weight: 700 !important;
    }
    &-800{
        font-weight: 800 !important;
    }

    // font size
    &-14{
        font-size: 0.875em !important;
    }
    &-16 {
        font-size: 1em !important;
    }
    &-18 {
        font-size: 1.125em !important;
    }
    &-20{
        font-size: 1.25em !important;
    }
    &-24{
        font-size: 1.5em !important;
    }

}

// text alignments
.text{
    &-left{
        text-align: left;
    }
    &-center{
        text-align: center;
    }
    &-right{
        text-align: right;
    }
    &-danger {
        color: $color-red;
    }
    &-capitalize {
        text-transform: capitalize !important;
    }
    &-uppercase {
        text-transform: uppercase !important;
    }
}

// mui label
label.Mui-focused{
    color: #102d5e !important;
}

// form css
.form{
    &-label {
        background-color: #fff;
        font-size: 1em !important;	
        font-weight: 500 !important;
    }
    &-group{
        .text-danger{
            font-size: 0.875em;
        }
        input, select{
            font-size: 1.125em;	
            padding: 16.5px 25px;	
            font-weight: 400;
            color: #333333;
            @media (max-width: 767px){
                padding: 16.5px 12px;	
                font-size: 1em;	
            }
        }
        .textarea{
            label{
                font-size: 1em !important;
                color: #555555 !important;
                font-weight: 500 !important;
                transform: translate(25px, 18px) scale(1);
                background: #fff;
                @media (max-width: 767px){
                    transform: translate(13px, 18px) scale(1);
                }
                &:after{
                    content: '*';
                    color: #f00;
                }
                &[class*='-shrink']{
                    color: #333 !important;
                    font-weight: 600 !important;
                    transform: translate(25px, -6px) scale(1);
                    @media (max-width: 767px){
                        transform: translate(13px, -6px) scale(1);
                    }
                }
            }
            textarea{
                font-size: 1.125em;
                overflow: auto;
                padding: 0 12px;
                color: #333333;
                @media (max-width: 767px){
                    padding: 0;
                    font-size: 1em;
                }
            }
        } 
        .form-label{
            transform: translate(20px, 18px) scale(1);	
            padding-left: 5px;	
            padding-right: 5px;	
            color: #555555;
            @media (max-width: 767px){
                transform: translate(8px, 18px) scale(1);	
            }
            &[class*='-shrink']{	
                color: #333 !important;
                font-weight: 600 !important;
                transform: translate(20px, -6px) scale(1);	
                @media (max-width: 767px){
                    transform: translate(8px, -6px) scale(0.8);	
                }
                &+div:hover{	
                    fieldset{	
                        border-color: #000 !important;	
                    }	
                }	
            }
            &.Mui-disabled{
                background: #f2f2f2;
                color: rgba(0, 0, 0, 0.38) !important;
                .text-danger{
                    opacity: 0.8;
                }
            }
            &+div{
                @media all and (-ms-high-contrast:none) {
                    border-radius: 4px;
                }
            }
        }
        &>div{
            fieldset{
                border-width: 1px !important;	
                border-color: #707070;	
                border-radius: 8px;
            }
            &:hover{	
                fieldset{	
                    border-color: #000 !important;   	
                }	
            }
            &.Mui-focused{
                fieldset{
                    border-color: #000 !important;
                    border-width: 2px !important; 
                }
            }
            &.Mui-disabled{
                input{
                    &+fieldset{
                        background: #f2f2f2;
                    }
                }
                fieldset{
                    border-color: rgba(0, 0, 0, 0.15) !important;
                }
                &:hover{
                    fieldset{
                        border-color: rgba(0, 0, 0, 0.15) !important;
                    }
                }
            }
        }
        
    }
    &-card{
        position: relative;
        border: none;
        padding: 30px 0;
        border-radius: 2px;
        border-bottom: 1px solid $color-disable;
        &-container{
            width: calc(100% + 30px) !important;
            margin: -15px !important;
            &>div{
                padding: 15px !important;
                &.shipping-link-wrapper{
                    padding-top: 0 !important;
                    text-align: right;
                    .btn-link{
                        font-size: 20px;
                        font-weight: 600;
                        text-decoration: none;
                        &:hover, &:focus{
                            box-shadow: none;
                            color: #0a6c9e;
                        }
                    }
                }
                &.grid-checkbox{
                    padding-bottom: 0 !important;
                }
            }
        }
        &:first-child{
            padding-top: 0;
        }
        &:first-of-type {
            padding-top: 5px;
        }
        &:last-child {
            padding-bottom: 0px;
            border-bottom: none;
        }
        h3 {
            font-size: 1.125em;
            color: $color-grey-dark;
            font-weight: 600;
        }
        .radio-group-shipping{
            margin-top: 15px;
            label{
                align-items: flex-start;
                div{
                    font-size: 18px;
                    @media (max-width: 767px){
                        font-size: 16px;
                    }
                }
                &>span{
                    &:last-child{
                        padding-top: 7px;
                    }
                }
            }
            span{
                svg{
                    color: #707070;
                }
                &.Mui-checked{
                    svg{
                        color: $color-primary;
                    }
                }
            }
        }
        .userinfo-wrapper{
            display: flex;
            font-size: 18px;
            padding-bottom: 10px !important;
            @media (max-width: 767px){
                font-size: 16px;
            }
            &-head{
                border-bottom: 1px solid #6E7175;
                margin-bottom: 20px;
                &.has-billing{
                    margin-top: 35px;
                    margin-bottom: 15px;
                }
            }
            .key{	
                color: #333;	
                font-weight: 600;	
                width: 80px;
                @media (max-width: 767px){
                    width: 60px;
                }
                &.full{
                    width: 100%;
                }
            }	
            .val{	
                color: #333;	
                font-weight: 400;	
                width: calc(100% - 80px);	
                padding-left: 5px;	
                word-break: break-word;
                @media (max-width: 767px){
                    width: calc(100% - 60px);	
                }
                &.full{
                    width: 100%;
                }
            }
        }
    }
    &-textarea {
        border: 1px solid #ccc;
        padding: 10px;
        resize: none;
        width: 100%;
        border-radius: 4px;
        font-size: 0.875em;
    }
    &-filled{
        padding: 0 15px;
        h5{
            margin-bottom: 10px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}

// checkbox with label
.custom-check{
    position: relative;
    svg{
        opacity: 0;
        margin-right: 5px;
    }
    &:before{
        content: '';
        height: 22px;
        width: 22px;
        border: 2px solid #b8b8b8;
        background-color: #fff;
        border-radius: 4px;
        position: absolute;
        left: 4px;
        top: 4px;
    }
    &.checked{
        &:after{
            content: '\f00c';
            font-weight: 700;
            position: absolute;
            top: 8px;
            left: 9px;
            font-family: "Font Awesome\ 5 Free", sans-serif;
            font-size: 1.125em;
            color: #000000;
        }
    }
}
.rememberCheck {
    span{ 
        font-size: 13px; 
        font-weight: 500; 
        color:$color-grey-dark;
        padding-top: 0;
        padding-bottom: 0;
    }
    .checkBx span{ 
        color: $color-primary;
    }
    .checkBx.Mui-disabled{
        span{
            color: rgba(0, 0, 0, 0.26);
        }
    }
}

/* loader*/
.loader{
    &-outer {
        position: fixed;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background-color: rgba(255, 255, 255, 0.5);
        z-index: 9999;
    }
    &-inner {
        color: $color-primary;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        &>div{
            color: $color-primary;
        }
    }
    &-inline{
        @media (max-width: $mobile-max-width) {
            margin-top: 20px !important;
        }
        svg{
            color: $color-primary;
        }
    }
}
.loaded {
    font-size: 1.2em;
    font-weight: bold;
    margin-bottom: 10px;
}

// padding css
.p{
    // verticle padding
    &y-0{
        padding-top: 0 !important;
        padding-bottom: 0 !important;
    }
    &t{
        &-10{
            padding-top: 10px;
        }
        &-15{
            padding-top: 15px;
        }
        &-20{
            padding-top: 20px;
        }
        &-25{
            padding-top: 25px;
        }
    }
    &l{
        &-0 {
            padding-left: 0px !important;
        }
        &-5 {
            padding-left: 5px !important;
        }
        &-10 {
            padding-left: 10px !important;
        }
    }
    &b{
        &-10{
            padding-bottom: 10px;
        }
        &-15{
            padding-bottom: 15px;
        }
    }
    &r{
        &-0{
            padding-right: 0 !important;
        }
    }
}

// margin css
.m{
    &t{
        &-5{
            margin-top: 5px;
        }
        &-10{
            margin-top: 10px;
        }
    }
    &b{
        &-0{
            margin-bottom: 0 !important;
        }
        &-5{
            margin-bottom: 5px !important;
        }
        &-15{
            margin-bottom: 15px !important;
        }
    }
    &x{
        &-auto{
            margin: 0 auto;
        }
    }
    &y{
        &-0{
            margin-top: 0;
            margin-bottom: 0;
        }
    }
}

// color css
.color{
    &-primary{
        color: $color-primary !important;
    }
    &-black{
        color: $color-black;
    }
    &-grey{
        color: #757575;
    }
    &-grey-dark{
        color: $color-grey-dark;
    }
    &-dark-blue{
        color: #00529C;
    }
}

// multi select
.select{
    &-multi{
        // react select without checkbox
        &.select-single{
            div[class*='-control']{
                &>div{
                    &:first-child{
                        &>div:last-child {
                            border: 1px solid #c4c4c4;
                            border-radius: 2px;
                        }
                    }
                    &:last-child{
                        display: block !important;
                        &>div{
                            display: none;
                            &:first-child{
                                display: block;
                                position: absolute;
                                padding: 0;
                                svg{
                                    opacity: 0;
                                }
                                &:after {
                                    content: "\f057";
                                    font-family: "Font Awesome\ 5 Free", sans-serif;
                                    position: relative;
                                    right: 12px;
                                    top: -1px;
                                    color: #fff;
                                    font-size: 12px;
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }
                input + div:empty:before {
                    content: 'Select Sizes';
                }
                div[class*='-singleValue']{
                    background-color: $color-primary;
                    color: #fff;
                    border-radius: 10px;
                    position: unset;
                    transform: none;
                    padding: 4px 30px 4px 9px;
                    font-size: 12px;
                    margin: 2px;
                }
            }
            div[class*=-option]{
                padding-left: 12px !important;
                &:before, &:after {
                    display: none !important;
                }
            }
        }

        // react select with checkbox
        &.react-select-multi{
            div[class*='-control'] {
                border: none;
                background-color: transparent;
                box-shadow: none;
                &:focus-within{
                    &:before{
                        border-color: #000;	
                        border-width: 2px;
                    }
                }
                &:before{
                    content: '';
                    height: 40px;
                    border: 1px solid #c4c4c4;
                    left: 0;
                    width: 100%;
                    position: absolute;
                    top: 0;
                    background-color: #fff;
                }
                &:after{
                    content: "\f107";
                    font-weight: 700;
                    position: absolute;
                    display: inline-block;
                    top: 0px;
                    right: 15px;
                    font-family: "Font Awesome\ 5 Free", sans-serif;
                    font-size: 1.8em;
                    -webkit-text-stroke: 1.8px #fff;
                    color: #4B4B4B;
                    pointer-events: none;
                    @media (max-width: $mobile-max-width) {
                        top: 2px;
                    }
                }
                input{
                    height: 0;
                }
                &>div:first-child {
                  &>div:last-child {
                    width: 100%;
                    padding: 5px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    margin: 0;
                    background-color: #fff;
                    height: 40px;
                    border: 1px solid $color-grey;
                    border-radius: 2px;
                    &:focus-within{
                        border-color: $color-primary;
                    }
                    &:after {
                        content: "\f107";
                        font-weight: 700;
                        position: absolute;
                        display: inline-block;
                        top: 50%;
                        transform: translateY(-50%);
                        right: 15px;
                        font-family: "Font Awesome\ 5 Free", sans-serif;
                        font-size: 1.8em;
                        -webkit-text-stroke: 1.8px #fff;
                        color: #4B4B4B;
                        pointer-events: none;
                    }
                    input {
                        padding-left: 8px !important;
                        padding-top: 2px !important;
                        font-size: 1em !important;
                      &+div {
                        visibility: visible !important;
                        height: auto !important;
                        overflow: initial !important;
                        top: 9px !important;
                        left: 13px !important;
                        color: transparent;
                        &:empty {
                          &:before {
                            color: $color-grey-dark;
                            font-weight: 500;
                          }
                        }
                      }
                    }
                  }
                }
                div[class*='-placeholder'] {
                    z-index: -1;
                    color: #000;
                    background: #fff;
                    position: absolute;
                    left: -9999px;
                }
                &>div {
                    padding: 40px 0 0 0;
                    min-height: auto;
                }
                div[class*='-multiValue'] {
                  background-color: $color-primary;
                  color: #fff;
                  border-radius: 10px;
                  @media all and (-ms-high-contrast:none) {
                    display: inline-block;
                    padding-right: 25px;
                    position: relative;
                  }
                  div{
                    color: #fff;
                    &:first-child {
                      flex: 1;
                      overflow: auto;
                      white-space: unset;
                      text-overflow: unset;
                      padding: 4px 3px 4px 9px;
                      font-size: 12px; 
                    }
                    &:last-child {
                      padding-right: 10px;
                      cursor: pointer;
                      @media all and (-ms-high-contrast:none) {
                        position: absolute;
                        top: 4px;
                        right: 0;
                      }
                      &:hover{
                          background-color: transparent;
                      }
                      svg{
                          opacity: 0;
                          width: 0;
                      }
                      &:after{
                        content: "\f057";
                        font-family: "Font Awesome\ 5 Free", sans-serif;
                        position: relative;
                        right: 0;
                        top: 0;
                        color: #fff;
                        font-size: 12px;
                        cursor: pointer;
                      }
                    }
                  }
                }
                &>div:last-child {
                  display: none;
                }
              }
              div[class*='-menu'] {
                width: 100%;
                div[class*=-option]{
                    padding: 5px 12px 5px 30px;
                    font-size: 14px;
                    position: relative;
                    &:before{
                        content: '';
                        color: #fff;
                        background-color: transparent;
                        position: absolute;
                        top: 8px;
                        left: 7px;
                        display: block;
                        width: 14px;
                        height: 14px;
                        border: 1px solid #adb5bd;
                        border-radius: 4px;
                    }
                    &:after{
                        content: "\f00c";
                        font-weight: 700;
                        font-family: "Font Awesome\ 5 Free", sans-serif;
                        position: absolute;
                        left: 10px;
                        font-size: 9px;
                        color: #fff;
                        top: 9px;
                        opacity: 0;
                    }
                    &.css-9gakcf-option{
                        background-color: #0096fb;
                        &:before{
                            border-color: #007bff;
                            background-color: #007bff;
                        }
                        &:after{
                            opacity: 1;
                        }
                    }
                }
              }
              &.brand-select{
                div[class*='-control'] > div:first-child{
                    &:before{
                        content: 'Select Brands';
                        position: absolute;
                        top: 9px;
