// react select
.react-select{
    &-theme{
        &.required{
            div[class*="-placeholder"]{
                &:after{
                    content: '*';
                    color: #f00;
                }
            }
        }
        span[class*="-indicatorSeparator"] {
            background-color: initial;
        }
        div[class*="-control"] {
            border-color: $color-grey;
            position: relative;
            &>div{
                &:last-child{
                    position: relative;
                    z-index: 2;
                }
            }
        }
        div[class*="-menu"] {
            z-index: 2;
        }
        div[class*="-indicatorContainer"]{
            position: relative;
            &:last-child{
                &:after {
                    content: "\f107";
                    font-weight: 700;
                    position: absolute;
                    display: inline-block;
                    top: 50%;
                    -webkit-transform: translateY(-50%);
                    transform: translateY(-50%);
                    right: 15px;
                    font-family: "Font Awesome\ 5 Free", sans-serif;
                    font-size: 1.8em;
                    -webkit-text-stroke: 1.8px #fff;
                    color: $color-grey-dark;
                }
                svg {
                    opacity: 0;
                }
            }
        }
        &>span{
            div[class*="-placeholder"] {
                color: rgba(0, 0, 0, 0.54);
            }
            &+div[class*="-control"]:after {
                content: "";
                position: absolute;
                top: -1px;
                left: -1px;
                border: 2px solid #000 !important;
                box-shadow: none;
                height: calc(100% + 2px);
                width: calc(100% + 2px);
                border-radius: 4px;
            }
            &+div[class*="-control"] {
                border-color: transparent;
                box-shadow: none;
            }
        }
    }

    // country select
    &-big{
        .css-1fhf3k1-control{
            background: #f2f2f2;
            border-color: rgba(0, 0, 0, 0.15) !important;
            div[class*="-placeholder"]{
                color: rgba(0, 0, 0, 0.38);
            }
            div[class*="-singleValue"]{
                color: rgba(0, 0, 0, 0.38);
            }
            div[class*="-indicatorContainer"]:last-child:after{
                color: rgba(0, 0, 0, 0.38);
                -webkit-text-stroke: 1.8px #f2f2f2;
            }
        }
        &> span + div[class*="-control"]:after{	
            border-radius: 8px;	
        }	
        div[class*="-singleValue"] {	
            font-weight: 400;	
            color: #333333;
            font-size: 1.125em;	
        }	
        div[class*="-control"] {	
            line-height: 40px;	
            border-width: 1px;	
            border-color: #9e9e9e;	
            border-radius: 8px;	
            &>div{	
                &:first-child{	
                    padding-left: 22px;
                    @media (max-width: 767px){
                        padding-left: 10px;
                    }
                }	
            }	
        }	
        div[class*="-placeholder"] {	
            font-size: 1em;	
            color: #545454;
            font-weight: 500;
        }	
    }
}

