                @media (max-width: 767px) {
                    height: 20px;
                    width: 20px;
                }
                &.checked{
                    &:before{
                        content: '';
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        height: 16px;
                        width: 16px;
                        background-color: $color-primary;
                        border-radius: 50%;
