                    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.24);
                }
                @media (max-width: 767px) {
                    padding: 0 18px 16px 18px;
                }
                h2{
                    margin: 0;
                    padding-top: 15px;
                    font-size: 1.5rem;
                    font-weight: 600;
                    line-height: 2;
                    color: #4b4b4b;
                    @media (max-width: $mobile-max-width) {
                        font-size: 1.3rem;
                    }
                }
            }
            .button-group {
                display: flex;
                padding: 12px 5px;
                margin-top: 15px;
                @media (max-width: $mobile-max-width) {
                    padding: 0;
                }
                @media (max-width: 767px) {
                    justify-content: flex-end;
                }
                button {
                    border-radius: 22px !important;
                    flex: 1;
                    margin: 0 9px !important;
                    @media (max-width: 991px){
                        padding-left: 5px;
                        padding-right: 5px;
                        font-size: 16px;
                    }
                    @media (max-width: 767px){
                        flex: none;
                        font-size: 0.875rem;
                    }
                    @media (max-width: 374px){
                        padding-left: 5px;
                        padding-right: 5px;
                        font-size: 14px;
                        margin: 0 5px !important;
                    }
                    @media (max-width: 359px){
                        min-width: 100px;
                    }
                    &:first-of-type {
                        margin-left: 0 !important;
                    }
                    &:last-of-type {
                        margin-right: 0 !important;
                    }
                }
            }
            .btn-link{
                font-size: 1.125em;
                font-weight: 600;
                text-decoration: none;
                &:hover, &:focus{
                    text-decoration: none;
                    box-shadow: none;
                    color: $color-primary-hover;
                }
            }
        }
    }
    &-form{
        &-wrapper{
            padding: 0 12px 0 0 !important;
            @media (max-width: 599px){
                order: 2;
                padding: 0 16px 0 0 !important;
            }
            &.tab-col{
                &+.summary-col{
                    margin-top: 0 !important;
                }
            }
        }
    }
}

.stepper{
    &-head{
        width: calc(960px - 200px);
        margin: 0 auto;
        justify-content: space-between;
        overflow: hidden;
        padding: 0px !important;
        @media (max-width: $mobile-max-width){
            width: 100%;
        }
        div[class^="MuiStepConnector"] {
            top: auto;
            bottom: 6px;
            left: calc(-100% - 70px);
            width: calc(100% + 100px);
            @media (max-width: $mobile-max-width){
                left: calc(-100% - 15px);
                width: calc(100% + 40px);
            }
            @media (max-width: 800px){
                left: calc(-100% + 30px);
                width: calc(100% - 5px);
            }
            &>span{
                border-top: none;
                background-image: linear-gradient(to right, #707070 33%, rgba(255, 255, 255, 0) 0%);
                background-position: bottom;
                background-size: 8px 1px;
                background-repeat: repeat-x;
                height: 1px;
                width: 100%;
                @media (max-width: $mobile-max-width){
                    display: none;
                }
            }
        }
        &>div{
            padding: 0px;
            flex: none;
            &:last-child{
                display: none;
            }
        }
    }
    &-col{
        flex: 1;
        position: relative;
        @media (max-width: $mobile-max-width) {
            max-width: 100%;
            flex-basis: 100%;
        }
        &-header{
            background-color: #FAFDFF;
            border-bottom: 1px solid $color-border;
            padding: 10px 0 0 0;
            position: relative;
            display: none;
            &.hide{
                display: none;
            }
            @media (max-width: $mobile-max-width){
                padding: 0 0 15px 0;
            }
            &:after{
                @media (max-width: $mobile-max-width){
                    content: '';
                    position: absolute;
                    bottom: 23px;
                    left: 48px;
                    width: calc(100% - 100px);
                    height: 1px;
                    background-image: linear-gradient(to right, #707070 33%, rgba(255, 255, 255, 0) 0%);
                    background-position: bottom;
                    background-size: 8px 1px;
                    background-repeat: repeat-x;
                }
                @media (max-width: 767px){
                    left: 45px;
                    width: calc(100% - 80px);
                }
            }
        }
    }
}
.step{
    &-head{
        height: 85px;
        flex-direction: column-reverse !important;
        align-items: flex-start !important;
        span[class^="MuiStepLabel-iconContainer"] {
            padding-left: 30px;
            z-index: 2;
            @media (max-width: $mobile-max-width){
                padding-left: 15px;
            }
        }
        span[class^="MuiStepLabel-labelContainer"] {
            &>span{
                display: flex;
                align-items: center;
                padding-bottom: 15px;
            }
        }
        .check{
            &-circle{
                background: #6E7175;
                height: 13px;
                width: 13px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                svg{
                    color: #fff;
                    opacity: 0;
                    width: 13px;
                    height: 13px;
                }
                &.checked{
                    background: #2FC61C;
                    svg{
                        opacity: 1;
                    }
                }
            }
        }
    }
}

// product filter
.product-filter{
    @media (max-width: $mobile-max-width){
        padding-left: 8px;
        padding-right: 8px;
    }
    &-title {
        position: relative;
        font-weight: 500;
    }
    &-wrapper{
        margin-bottom: 30px;
        background-color: #fff;
        padding: 30px 20px;
        @media (max-width: $mobile-max-width){
            padding-bottom: 0;
        }
        .filter-desc{
            padding-top: 0 !important;
            padding-bottom: 0 !important;
            p{
                font-size: 0.75em;
                color: $color-grey-dark;
                font-weight: 400;
            }
        }
    }
    &-group{
        &>div{
            @media (max-width: 599px){
                padding-bottom: 0 !important;
            }       
            &:first-child{
                @media (max-width: 599px){
                    padding-top: 5px !important;
                }       
            }
        }
    }
    &>div {
        @media (max-width: $mobile-max-width){
            width: 100%;
        }
    }
}

// quantity counter
.counter{
    &-grid {
        max-width: 110px !important;
        @media all and (-ms-high-contrast:none) {
            max-width: none !important;
        }
        @media (max-width: 767px){
            margin: 15px 0;
        }
        &-button{
            &.btn-mui{
                padding-top: 5px;
                padding-bottom: 5px;
                margin-left: 10px;
            }
        }
    }
    &-wrapper {
        @supports (-ms-ime-align: auto) {
            width: 110px;
        }
        @media all and (-ms-high-contrast:none) {
            width: 110px;
            margin: 0 auto;
        }
        &.qty {
            position: relative;
            top: -1px;
        }
        button {
            padding: 0;
            @media (max-width: 767px){
                height: 24px;
                width: 24px;
            }
            span{
                @media (max-width: 767px){
                    height: 24px;
                    width: 24px;
                }
            }
            svg{
                @media (max-width: 767px){
                    overflow: initial;
                    transform: scale(0.8);
                }
                path{
                    fill:$color-primary
                }
            }
            &:hover {
                background-color: transparent;
            }
        }
        input {
            border: none;
            width: 100%;
            padding: 0px;
            text-align: center;
            min-height: 25px;
            font-size: 1.125rem;
            font-weight: 500;
            color: #3c3c3c;
            @media (max-width: 767px){
                font-size: 0.875rem;
            }
            &:focus{
                outline: none;
            }
            &:disabled{
                background: #fff;
                opacity: 1;
                -webkit-text-fill-color: $color-grey-dark;
            }
        }
    }
}

.cart-total-header{
    position: fixed !important;
    top: 30px;
    right: 140px;
    display: none !important;
    z-index: 1301 !important;
    min-width: auto !important;
    width: auto !important;
    padding: 3px 13px !important;
    font-size: 1em !important;
    @media (max-width: 959px){
        display: none !important;
        right: 230px;
        top: 35px;
    }
    @media (max-width: 767px){
        right: 45px;
        top: 33px;
    }
    @media (max-width: 599px){
        top: 28px;
    }
    @media (max-width: 420px){
        right: 45px;
        top: 18px;
    }
}

div{
