            &.btn-blue{
                background-color: $color-primary;
                border-color: $color-primary;
                color: #fff;
                &:hover, &:focus{
                    background-color: $color-primary-hover;
                    border-color: $color-primary-hover;
                }
                &.Mui-disabled{
                    border-color: $color-border;
                    background-color: $color-border;
                    color: $color-grey-dark;
                }
            }
        }
        .button-group{	
            margin-top: 60px;	
        }
        .react-select-big{
            &> span + div[class*="-control"]:after{	
                border-width: 1px;	
                height: 100%;
                width: 100%;
                border-radius: 8px;	
                top: -1px;
                left: -1px;
            }	
        }
    }
    &-card{
        padding: 10px;
        box-shadow: 0px 1px 3px #00000034 !important;
        border-radius: 15px !important;
        border: 1px solid #DDDDDD;
        overflow: unset !important;
        &.card-3{
            display: flex;
            @media (max-width: $mobile-max-width){
                flex-flow: column;
            }
            .profile-card-single{
                flex-grow: 0;
                max-width: 33.333333%;
                flex-basis: 33.333333%;
                &:nth-child(2){
                    flex-grow: 0;
                    max-width: 40%;
                    flex-basis: 40%;
                    @media (max-width: $mobile-max-width){
                        max-width: 100%;
                        flex-basis: 100%;
                    }
                }
                @media (max-width: $mobile-max-width){
                    max-width: 100%;
                    flex-basis: 100%;
                }
            }
        }
        &.card-2{
            display: flex;
            .profile-card-single{
                flex-grow: 0;
                max-width: 50%;
                flex-basis: 50%;
            }
        }
        &-single{
            border-right: 1px solid #ccc;
            padding: 20px 34px;
            @media (max-width: $mobile-max-width){
                padding: 15px;
                border-right: none;
                border-bottom: 1px solid #ccc;
            }
            @media (max-width: 767px){
                padding: 15px 5px;
            }
            &:last-child{
                border-right: none;
                @media (max-width: $mobile-max-width){
                    border-bottom: none !important;
                }
            }
            span{
                svg{
                    color: #707070;
                }
                &.Mui-checked{
                    svg{
                        color: $color-primary;
                    }
                }
            }
        }
        &-form{
            padding: 40px;
            @media (max-width: $mobile-max-width){
                padding: 30px 15px;
            }
        }
        .key{
            color: #333333;
            font-size: 18px;
            font-weight: 600;
            @media (max-width: 767px){
                font-size: 16px;
            }
        }
        .val{
            color: #333333;
            font-size: 18px;
            word-wrap: break-word;
            @media (max-width: 767px){
                font-size: 16px;
            }
            @media (max-width: 420px){
                font-size: 14px;
            }
        }
        .form-card-container{
            @media (max-width: $mobile-max-width){
                width: calc(100% + 20px) !important;
                margin: -10px !important;
            }
            &>div {
                @media (max-width: $mobile-max-width){
                    padding: 10px !important;
                }
            }
        }
        .form-group{
            input, select{
                @media (max-width: $mobile-max-width){
                    padding: 16.5px 15px;
                }
            }
            .form-label{
                @media (max-width: $mobile-max-width){
                    transform: translate(10px, 18px) scale(1);
                }
                &[class*='-shrink']{
                    @media (max-width: $mobile-max-width){
                        transform: translate(10px, -6px) scale(1);
                    }
                }
            }
        }
    }
}
.hero-text .hero-header,
.home-text .home-title {
    font-family: 'RailroadGothic' !important;
}
.no-items{
    text-align: center;
    padding: 20px;
}
.IE-only{
    display: none !important;   
    @media all and (-ms-high-contrast:none) {
        display: block !important;
    }
}
.out-of-stock {
    color: $color-white;
}