@import './variables.scss';
@mixin standardLink($color, $hover) {
  color: $color;
  text-transform: uppercase;
  font-size: $font18;
  line-height: $font26;
  letter-spacing: 0.51px;
  text-decoration: none;
  font-weight: 500;
  &:hover,
  &:focus {
    color: $hover;
  }
}
@mixin standardButton($color, $bg, $hoverBg, $hoverColor) {
  color: $color;
  background-color: $bg;
  text-transform: uppercase;
  font-size: $font18;
  line-height: $font26;
  letter-spacing: 0.51px;
  padding: 6px 16px;
  &:hover,
  &:focus {
    color: $hoverColor;
    background-color: $hoverBg;
  }
}
@mixin pageHeading() {
  color: $blue-light;
  text-transform: uppercase;
  font-size: $font60;
  line-height: 1;
  letter-spacing: 1.71px;
}
@mixin pageInnerHeading($color) {
  color: $color;
  text-transform: uppercase;
  font-size: $font55;
  line-height: 1;
  letter-spacing: 1.57px;
}
@mixin popupHeading() {
  color: $blue-light;
  text-transform: uppercase;
  font-size: $font52;
  line-height: 1;
  letter-spacing: 1.49px;
}
@mixin homeHeading($c) {
  color: $c;
  text-transform: uppercase;
  font-size: $font60;
  line-height: 1;
}
@mixin pageContent($color) {
  color: $color;
  font-size: $font16;
  line-height: $font22;
  letter-spacing: 0.51px;
}
@mixin thankyouHeading($color) {
  color: $color;
  font-size: $font30;
  line-height: 1;
  letter-spacing: 0.51px;
}
@mixin formHeading($color) {
  color: $color;
  font-size: $font16;
  line-height: 1;
  letter-spacing: 1.16px;
}
