@import "./../../../styles/commonStyle/commonStyle.scss";

.modal-wrapper {
  &-container{
    z-index: 9999 !important;
  }
  &.modal-paper {
    text-align: center;
    background: $color-white 0% 0% no-repeat padding-box;
    box-shadow: 3px 3px 5px $color-box-shadow;
    border: 1px solid #707070;
    border-radius: 16px;
    padding: 40px 50px;
    max-width: 400px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -200px;
    margin-top: -150px;
    @media (max-width:767px) {
      margin-left: -180px;
      max-width: 360px;
    }
  }
  button {
    font-size: $font18;
    font-weight: 500;
    line-height: $font28;
    padding: 5px 24px;
    color: $color-white;
    text-transform: capitalize;
    display: inline-block;
    margin: 10px;
    border-radius: 21px;
    &.accept {
      background: $color-dark-red;
      border: 2px solid $color-dark-red;
    }

    &.reject {
      background: $color-white;
      color: $color-grey-dark;
      border: 2px solid #6e7175;
    }
  }

  .heading {
    font-size: $font18;
    font-weight: 500;
    line-height: $font28;
    margin-bottom: 30px;
    display: block;
    i {
      display: block;
      font-size: $font60;
      margin-bottom: 15px;
      @media (max-width:767px) {
        font-size: $font48;
      }
    }
  }
}
