@import "./../../styles/commonStyle/commonStyle.scss";
.product-box {
  &-bundle-card {
    padding: 10px 20px 10px 20px;
    border-radius: 26px !important;
    background: $color-white 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px $color-box-shadow !important;
    border: 1px solid $color-border;
    width: 100%;
    outline: 0;
    opacity: 1;
    position: relative;
    @media (max-width: 1025px) {
      cursor: default !important;
    }
    @media (max-width: 767px) {
      padding: 10px;
    }
    &:hover {
      @media (min-width: 1025px) {
        transition: transform 0.5s !important;
      }
    }
    .quantity-box {
      &.show {
        overflow-y: scroll;
      }
    }
    &.active {
      overflow: unset;
      background-color: #4b4b4b;
      .more-vert,
      .product-brand,
      .product-heading,
      .bundle-card-info,
      .quantity-count-wrapper input {
        color: #ffffff;
      }
      .media-card-wrapper {
        position: relative;
        transform: translateY(-40px);
        transition: all 0.3s ease-out;
        margin-bottom: 15px;
        @media (max-width: $mobile-max-width) {
          transform: none;
          height: 135px;
        }
        @media (max-width: 767px) {
          margin-bottom: 10px;
          height: 110px;
        }
        img {
          @media (min-width: 991px) {
            transform: scale(0.98);
            transition: all 0.3s ease-out;
          }
        }
      }
      .shadow {
        position: absolute;
        width: 100%;
        height: 54px;
        left: 0;
        top: 158px;
        background: radial-gradient(
          ellipse at 50% 33%,
          rgba(0, 0, 0, 0.3),
          rgba(97, 97, 97, 0) 40%
        );
        @media (max-width: $mobile-max-width) {
          display: none;
          top: 138px;
        }
        @media (max-width: 767px) {
          height: 34px;
          top: 106px;
        }
      }
    }
    .star {
      color: $color-yellow-hover;
    }
    .more-vert {
      color: $color-primary;
    }
    .product-options {
      position: absolute;
      top: 2px;
      left: 1px;
      width: calc(100% - 2px);
      z-index: 1;
      button {
        padding: 5px;
        svg {
          width: 30px;
          height: 30px;
          @media (max-width: 767px) {
            width: 24px;
            height: 24px;
          }
        }
      }
    }
    .product-brand {
      font-size: $font14;
      color: $color-grey-dark;
      margin-bottom: 5px;
      @media (max-width: 375px) {
        font-size: 11px;
      }
      @media (max-width: 374px) {
        font-size: 10px;
      }
      @media (max-width: 359px) {
        font-size: $font14;
      }
    }
    .product-heading {
      display: block;
      width: 100%;
      position: relative;
      margin-bottom: 5px;
      min-height: 40px;
      font-size: $font16;
      line-height: $font20;
      color: $color-grey-dark;
      span {
        display: inline-block;
        white-space: nowrap;
      }
    }
    .product-action {
      text-align: center;
      margin-top: 30px;
      @media (max-width: $mobile-max-width) {
        margin-top: 15px;
      }
      @media (max-width: 767px) {
        margin-top: 5px;
      }
    }
    .bundle-card-info {
      font-size: $font16;
      font-weight: 500;
      line-height: $font14;
      padding: 0;
      margin-bottom: 20px;
      color: #4b4b4b;
    }
    .out-of-stock {
      padding-top: 4px;
      padding-bottom: 4px;
      text-align: center;
    }
    .media-card-wrapper {
      padding: 0 !important;
      height: 160px;
      position: relative;
      display: block;
      margin: 15px 0 25px 15px;
      background: transparent;
      width: calc(100% - 30px);
      @media (max-width: $mobile-max-width) {
        height: 140px;
        margin: 25px 0 15px 15px;
      }
      @media (max-width: 767px) {
        height: 110px;
        width: 100%;
        margin: 20px 0 10px 0;
      }
      &:hover {
        background: transparent;
      }
      span[class*="MuiTouchRipple"] {
        display: none;
      }
      img {
        padding: 0 10px;
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        max-height: 160px;
        font-size: $font14;
        line-height: $font16;
        @media (max-width: 767px) {
          max-height: 100%;
        }
      }
    }
    .selected-product {
      color: #c04319;
      padding: 0;
      font-size: $font24;
      font-weight: 600;
      text-transform: lowercase;
      display: inline-block;
      &:hover {
        background: none;
      }
    }
  }
}
.quantity-box {
  height: 130px;
  overflow-y: hidden;
  position: relative;
  @media (max-width: 991px) {
    height: 110px;
  }
  @media (max-width: 767px) {
    height: 100px;
  }
}

.sizes-arrow {
  &-down {
    position: absolute;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    @media (max-width: 767px) {
      bottom: 8px;
    }
  }
}

div.product-grid-container {
  &.lists {
    .btn-mui {
      padding: 6px 16px;
      text-align: center;
      text-decoration: none;
      cursor: pointer;
      &[disabled] {
        cursor: default;
      }
      &.btn-red {
        &[disabled] {
          color: $color-red !important;
          pointer-events: none;
          &:hover {
            color: $color-red !important;
          }
        }
      }
    }
    & > div {
      &.noproducts-grid {
        max-width: 75%;
        flex-basis: 75%;
        @media (max-width: $mobile-max-width) {
          max-width: 100%;
          flex-basis: 100%;
        }
      }
      &.mix-count {
        padding: 0 !important;
      }
    }
  }
  & > div {
    outline: 0;
    @media (max-width: 991px) {
      max-width: 33.33%;
      flex-basis: 33.33%;
    }
    @media (max-width: 767px) {
      max-width: 50%;
      flex-basis: 50%;
      padding: 8px !important;
    }
    @media (max-width: 359px) {
      max-width: 100%;
      flex-basis: 100%;
    }
  }
}
