@import "./../../../styles/commonStyle/commonStyle.scss";
.quantity-count {
  &-contain {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 13px;
    @media (max-width: 767px) {
      font-size: $font14;
    }
    .size-wrapper {
      color: $color-white;
      font-size: $font18;
    }
    .price {
      font-weight: 600;
      line-height: $font16;
    }
  }
  &-wrapper {
    display: flex;
    cursor: default;
    .trash-icon {
      color: $color-white;
      font-size: $font30;
      vertical-align: bottom;
    }
    button {
      padding: 0;
      color: $color-white;
      background: none !important;
      min-width: 22px;
      &[disabled] {
        color: $color-white;
      }
      .qty-icon {
        font-size: $font36;
        @media (max-width: 767px) {
          font-size: $font24;
          width: 20px;
        }
      }
    }
    input {
      width: 50px;
      text-align: center;
      border: none;
      font-size: $font16;
      font-weight: 500;
      color: $color-white;
      background-color: transparent;
      @media (max-width: 767px) {
        width: 36px;
      }
      &:active,
      &:hover {
        border: none;
        outline: none;
      }
    }
  }
}
