@import "./../../../styles/commonStyle/commonStyle.scss";
.homepage-authenticated-user {
  .home-offer {
    top: 90px !important;
    z-index: 999 !important;
    @media (max-width: 767px) {
      top: 90px !important;
    }
    @media (max-width: 420px) {
      top: 65px !important;
    }
  }
}
.hero-banner {
  position: relative;
  .user-name {
    color: $color-white;
    font-weight: 500;
    margin: 40px auto;
    text-align: center;
    font-size: $font16;
    @media (max-width: 991px) {
      margin: 40px auto 20px;
    }
    .welcome-msg {
      font-size: $font60;
      line-height: $font12;
      display: block;
      @media (max-width: 991px) {
        font-size: $font42;
      }
    }
    .name {
      font-size: $font90;
      line-height: $font12;
      display: block;
      text-transform: uppercase;
      @media (max-width: 767px) {
        font-size: $font48;
      }
    }
  }
}

@media not all and (min-resolution:.001dpcm) { 
    .hero-banner .user-name .welcome-msg{
      margin-bottom: 19px;
    }
}