@import "./../../styles/commonStyle/commonStyle.scss";
.popular-products {
  &-container {
    display: block;
    width: 100%;
    &-inner {
      max-width: 960px;
      flex-wrap: wrap;
      margin: 0 auto;
      display: flex;
    }
  }
  &-show-all {
    display: flex;
    flex-wrap: wrap;
    .bundle-card {
      width: 23%;
      height: auto;
      margin: 2% 1% !important;
      @media (max-width: 991px) {
        width: 31%;
      }
      @media (max-width: 767px) {
        margin: 3% !important;
        width: 44%;
      }
    }
  }
  &-slider {
    .rec {
      &-arrow {
        &:after {
          border-color: transparent transparent $color-orange $color-orange;
          box-shadow: -3px 3px 3px 0 $color-box-shadow;
        }
      }
    }
  }
  &-list {
    position: relative;
    text-align: center;
    display: flex;
    margin-bottom: 30px;
    width: 100%;
  }
}
