@import "./../../../styles/commonStyle/commonStyle.scss";
.snacks {
  &-slider {
    position: relative;
    min-height: 400px;
    .rec {
      &-slider {
        &-container {
          margin: 0 auto;
        }
        > div {
          outline: 0;
        }
      }
      &-item-wrapper {
        outline: 0;
      }
      &-arrow {
        position: absolute;
        z-index: 9;
        top: 50%;
        margin-top: -52px;
        min-width: 10px;
        @media (max-width: 767px) {
          margin-top: -37px;
        }
        &:after {
          content: "";
          width: 0;
          height: 0;
          top: 50%;
          left: 50%;
          box-sizing: border-box;
          border: 30px solid $color-black;
          border-color: transparent transparent $color-white $color-white;
          transform-origin: 0 0;
          box-shadow: -3px 3px 3px 0 $color-box-shadow;
          @media (max-width: 767px) {
            border: 15px solid $color-black;
            border-color: transparent transparent $color-white $color-white;
          }
        }
        &:hover {
          background: none;
          box-shadow: 0 0 0;
        }
        &:disabled {
          display: none;
        }
        &.rec-arrow-left {
          left: 0;
          margin-left: -30px;
          transform: rotate(45deg);
          @media (max-width: 1024px) {
            margin-left: -25px;
          }
          @media (max-width: 800px) {
            margin-left: 0;
          }
        }
        &.rec-arrow-right {
          margin-right: -30px;
          right: 0;
          transform: rotate(-135deg);
          @media (max-width: 1024px) {
            margin-right: -25px;
          }
          @media (max-width: 800px) {
            margin-right: 0;
          }
        }
      }
    }
  }
}
