@import "./../../../styles/commonStyle/commonStyle.scss";

.home-box {
  &-wrapper {
    left: 0;
    padding: 0 30px;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 9;
    &-inner {
      max-width: 960px;
      margin: 30px auto;
      width: calc(100% - 60px);
      @media (max-width: 991px) {
        margin: 20px auto;
        padding: 0;
        width: 100%;
      }
    }
  }
  &-item {
    display: inline-block;
    vertical-align: top;
    &-link {
      text-decoration: none;
      position: relative;
      cursor: pointer;
      padding: 30px 35px;
      height: 100%;
      display: block;
      border-radius: 25px;
      background: url(https://share.snacks.com/images/bg_modal.png) no-repeat
        right bottom #fff;
      margin: 15px;
      box-shadow: 0px 3px 6px $color-box-shadow;
      text-align: center;
      min-height: 480px;
      background-size: 100% auto;

      @media (max-width: 991px) {
        padding: 20px 25px;
        min-height: 380px;
      }
      @media (max-width: 767px) and (orientation: portrait) {
        min-height: 360px;
      }
    }
    .heading {
      color: #c04319;
      margin: 0 0 20px;
      font-size: $font26;
      font-weight: 600;
      line-height: $font42;
      @media (max-width: 991px) {
        font-size: $font20;
        line-height: $font36;
      }
    }
    .description {
      font-size: $font17;
      font-weight: 500;
      line-height: $font24;
      text-align: left;
      color: #000;
      margin: 0;
      @media (max-width: 991px) {
        font-size: $font14;
        line-height: $font24;
      }
    }
    .link {
      position: absolute;
      left: 35px;
      bottom: 20px;
      font-size: $font18;
      font-weight: 600;
      line-height: $font30;
      padding: 0;
      text-transform: capitalize;
      @media (max-width: 800px) {
        font-size: $font16;
        line-height: $font24;
      }
      i {
        margin-left: 5px;
      }
      &:hover {
        background: none;
      }
    }
    &.previous-orders {
      .home-box-item-link {
        background-image: url(/images/previous-order-box.png);
      }
      .link {
        color: #000;
      }
    }
    &.all-products {
      .home-box-item-link {
        background-image: url(/images/product-box.png);
      }
      .link {
        color: #fff;
      }
    }
  }
}
