@import "./../../../styles/commonStyle/commonStyle.scss";
.footer{
  padding-top: 25px;
  padding-bottom: 15px;
  background-image: linear-gradient(to right, #cc2500 , #f2a326);
  position: relative;
  border-top: 10px solid $color-primary;
  @media (max-width: $mobile-max-width){
      padding: 10px;
  }
  .logo{
      max-width: 100%;
      @media (max-width: $mobile-max-width){
          max-width: 30px;
      }
      &-row{
        @media (max-width: $mobile-max-width){
          position: absolute;
          top: -30px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
  }
  .container-root{
    max-width: 1300px !important;
      @media (max-width: $mobile-max-width){
          margin: 0 !important;
          padding: 0px !important;
          width: 100% !important;
      }
  }
  .sublinks{
      padding: 0 70px 0 0;
      display: flex;
      flex-wrap: wrap;
      li{
          width: 33.33%;
          padding: 5px 2px;
          @media (max-width: $mobile-max-width){
            width: 50%;
          }
          a{
              color: $color-white;
              font-size: $font14;
              font-weight: 500;
              cursor: pointer;
              text-decoration: none;
              transition: all .5s ease;
              @media (max-width: $mobile-max-width){
                  font-size: 0.875em;
              }
              &:hover{
                color: $color-yellow;
              }
          }
      }
  }
  .social-links{
      display: inline-flex;
      padding: 0;
      li{
          padding: 2px 8px;
          vertical-align: top;
          &:last-child{
            padding-right: 0;
          }
          @media (max-width: $mobile-max-width){
              padding: 2px 10px;
          }   
          svg{
              height: 18px;
              path{
                  fill: $color-white;
                  transition: all .5s ease;
              }
              &:hover{
                  path{
                      fill: $color-white;
                  }
              }
          }
          span{
              z-index: -1;
              color: $color-black;
              background: #fff;
              position: absolute;
              left: -9999px;
          }
      }
  }
  .copyright{
      text-align: right;
      padding-top: 10px;
      &-text{
          color: $color-white;
          font-size: $font14;
          font-weight: 500;
          margin: 10px 0 0 0;
      }
      @media (max-width: 767px){
        text-align: center;
      }
  }

  // to add footer space in mobile
  &-steps{
      @media (max-width: $mobile-max-width){
          padding-bottom: 80px;
      }
  }
}