.account{
    &-info{
        background-color: rgba(196, 196, 196, 0.24);
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.10);
        text-align: center;
        padding: 30px 15px;
        h2{
            font-size: 1.4em;
            font-weight: 500;
            color: #4b4b4b;
            @media (max-width: 767px) {
                font-size: 1.2em;
            }
        }
        h3{
            font-size: 1.375em;
            font-weight: 300;
            color: #4b4b4b;
            margin-top: 8px;
            @media (max-width: 767px) {
                font-size: 1.125em;
            }
        }
    }
    &-form{
        width: 350px;
        margin: 50px auto;
        @media (max-width: 767px) {
            width: 100%;
        }
        .form-card{
            border: none;
            h3{
                font-size: 1.6em;
            }
            p{
                color: #767676;
                margin-top: 8px;
                margin-bottom: 40px;
                display: flex;
                align-items: center;
                font-size: 1.125em;
                .btn-link{
                    border: none;
                    padding: 0;
                    margin-left: 5px;
                    font-weight: 400;
                    font-size: 1em
                }
            }
        }
        .tnc{
            p{
                color: #767676;
                margin-top: 5px;
                margin-bottom: 40px;
                display: block;
                a{
                    color: #767676;
                }
            }
        }
        .button-group{
            display: flex;
            .btn-mui{
                @media (max-width: 767px) {
                    font-size: 1.1em;
                }
                &:not(:last-child) {
                    margin-right: 10px;
                    margin-bottom: 0;
                }
            }
            .btn-outlined{
                min-width: 120px;
                @media (max-width: 360px) {
                    min-width: 100px;
                }
            }
            .btn-blue{
                flex: 1;
                background-color: #0085ca;
                border-color: #0085ca;
                color: #fff;
                &:hover{
                    background-color: #0677b1;
                    border-color: #0677b1;
                }
                &[disabled] {
                    border-color: #DDDDDD;
                    background-color: #DDDDDD; 
                    color:rgba(75, 75, 75, 0.5);
                }
            }
        }
    }
}