@import './../../styles/commonStyle/commonStyle.scss';
div {
  &.accordion {
    &-wrapper {
      box-shadow: none;
      margin: 0 !important;
      background-color: transparent;
      &:before {
        opacity: 0;
      }
      p {
        color: $color-grey-dark;
        font-size: 0.75em;
      }
    }
    &-header {
      border: none;
      display: block;
      margin: 36px 0 0 0;
      padding: 0;
      min-height: auto !important;
      & > div {
        margin: 0 !important;
        padding: 0;
      }
      .fa-angle-up {
        -webkit-text-stroke: 1.5px #fff;
        color: #02a6e1;
        font-size: 28px;
      }
      div {
        display: inline-block;
        h2 {
          font-size: 1.125rem;
          font-weight: 600;
          line-height: 1.56;
          margin-right: 14px;
          color: #fff;
        }
        span {
          .fa {
            color: #fff;
          }
        }
      }
    }
    &-body {
      padding: 10px 0;
      img {
        max-width: 80%;
        max-height: 300px;
      }
    }
  }
}
.product {
  &-detail {
    &-image {
      padding-right: 40px;
      position: absolute;
      height: 300px;
      top: -20px;
      &-outer{
        min-height: 280px;
      }
      img {
        max-width: 100%;
        max-height: 100%;
      }
      .shadow{
        height: 54px;
        background: radial-gradient(ellipse at 50% 33%, rgba(0, 0, 0, 0.3), rgba(97, 97, 97, 0) 40%);
      }
    }
    &-wrapper {
      padding: 36px;
      border-radius: 10px;
      @media (max-width: 959px) {
        padding-top: 15px;
      }
      @media (max-width: $mobile-max-width) {
        padding-top: 0;
        position: relative;

        .product-info-container {
          flex-direction: column-reverse;
          .product-detail-image {
            height: 182px;
            margin: 0 0 24px;
            width: 195px;
          }
          .product-info-div {
            flex: unset;
            .product-brand {
              font-size: 0.875rem;
            }
            .product-name {
              font-size: 1.25rem;
            }
            .product-desc {
              font-size: 0.75rem;
            }
          }
        }
      }
    }
    &-actions {
      padding: 5px 0 0 0;
      @media (max-width: 767px){
          padding: 10px 0;
          height: auto;
      }
      @media all and (-ms-high-contrast:none) {
          display: flex;
          align-items: center;
      }
      .quantity-count-wrapper {
        display: inline-block;
        width: auto;
      }
    }
  }
  &-desc {
    font-size: 0.875rem;
    line-height: 1.29;
    color: #fff;
  }
  &-brand {
    font-size: 1.125rem;
    line-height: 2;
    color: #fff;
  }
  &-name {
    font-size: 1.5rem;
    font-weight: 600;
    line-height: 1.17;
    color: #fff;
    margin-bottom: 18px;
  }
}